html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-arr-full {
  width: 1em;
  height: 1em;
  fill: #121313;
}

.icon-arr-sm {
  width: 2.43em;
  height: 1em;
  fill: none;
}

.icon-arr {
  width: 0.29em;
  height: 1em;
  fill: none;
}

.icon-check {
  width: 1em;
  height: 1em;
  fill: #121313;
}

.icon-leaf {
  width: 0.78em;
  height: 1em;
  fill: #000;
}

html,
body {
  height: 100%;
}

body {
  position: relative;
  background: #f2f2f2;
  line-height: 1.4;
  font-size: 16px;
  color: #000;
  min-width: 320px;
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 400;
}

a {
  text-decoration: none;
}

button {
  background: transparent;
}

img {
  max-width: 100%;
}

.link {
  font-size: 22px;
  color: #000;
  white-space: nowrap;
  transition: opacity 0.3s;
}

.link:hover {
  opacity: .8;
}

@media only screen and (max-width: 1023px) {
  .link {
    font-size: 18px;
  }
}

.link__text {
  margin-right: 20px;
  line-height: 1;
}

.link__pic {
  line-height: .5;
}

.link > span {
  display: inline-block;
  vertical-align: middle;
}

.link .icon {
  width: 5px;
  height: 20px;
  stroke: #000;
}

.container {
  position: relative;
  max-width: 1300px;
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

.container_height {
  min-height: 100%;
}

.l {
  font-size: 0;
  margin: 0 -25px;
}

@media only screen and (max-width: 1200px) {
  .l {
    margin: 0 -20px;
  }
}

@media only screen and (max-width: 960px) {
  .l {
    margin: 0;
  }
}

.l__col {
  display: inline-block;
  vertical-align: top;
  max-width: calc(33.3% - 50px);
  width: 100%;
  margin: 0 25px;
}

@media only screen and (max-width: 1200px) {
  .l__col {
    max-width: calc(33.3% - 40px);
    margin: 0 20px;
  }
}

@media only screen and (max-width: 960px) {
  .l__col {
    display: block;
    max-width: 400px;
    width: 100%;
    margin: 0 auto 20px auto;
  }
}

.title {
  font-size: 25px;
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 400;
}

@media only screen and (max-width: 1023px) {
  .title {
    font-size: 20px;
  }
}

h4,
.h4 {
  font-size: 16px;
}

@media only screen and (max-width: 1023px) {
  h4,
  .h4 {
    font-size: 14px;
  }
}

h5,
.h5 {
  font-size: 16px;
}

@media only screen and (max-width: 1023px) {
  h5,
  .h5 {
    font-size: 14px;
  }
}

.header {
  min-height: 70px;
  background: #000;
}

.breadcrumbs {
  background: #000;
}

@media only screen and (max-width: 767px) {
  .breadcrumbs {
    display: none;
  }
}

.breadcrumbs__list {
  padding: 8px 0;
}

.breadcrumbs__item {
  display: inline-block;
  vertical-align: middle;
}

.breadcrumbs__item:not(:last-child):after {
  content: '>';
  display: inline-block;
  vertical-align: middle;
  font-size: 25px;
  color: #fff;
}

.breadcrumbs__item:hover:not(:last-child) .breadcrumbs__link {
  opacity: .7;
}

.breadcrumbs__item:last-child .breadcrumbs__link {
  pointer-events: none;
}

.breadcrumbs__link {
  color: #f2f2f2;
  transition: opacity 0.3s;
}

.catalog {
  margin: 55px 0 50px 0;
}

@media only screen and (max-width: 960px) {
  .catalog {
    margin: 25px 0;
  }
}

.catalog__item {
  padding: 20px 30px 17px 25px;
  background: #e6e6e6;
  margin-bottom: 30px;
}

.catalog__line {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
}

.catalog__line_mod {
  justify-content: flex-end;
}

.catalog__head {
  margin-bottom: 15px;
}

.catalog__title {
  color: #000;
  max-width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.catalog__text {
  max-width: 80%;
  margin-bottom: 20px;
  max-height: 92px;
  overflow: auto;
  line-height: 1.2;
}

@media only screen and (max-width: 960px) {
  .catalog__text {
    max-height: none;
  }
}

.catalog__caption {
  margin-bottom: 10px;
}

.btn {
  display: inline-block;
  vertical-align: middle;
  font-size: 16px;
  line-height: 1;
  padding: 9px 8px 8px 16px;
  color: #fff;
  text-align: center;
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 500;
  transition: opacity 0.3s;
}

@media only screen and (max-width: 1023px) {
  .btn {
    font-size: 14px;
  }
}

.btn:hover {
  opacity: .8;
}

.btn_black {
  background: #000;
}

.btn_blue {
  background: #0071BC;
}

.btn_sm {
  font-size: 10px;
  padding: 3px 9px;
}

.btn_mod {
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 600;
}

.table__thead {
  border-bottom: 0.85px solid #A5A5A5;
}

.table__thead td {
  font-size: 11px;
  color: #000;
  padding: 9px 10px 9px 0;
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 500;
}

.table__text, .table__link {
  font-size: 16px;
  color: #000;
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 400;
}

@media only screen and (max-width: 960px) {
  .table__text, .table__link {
    font-size: 14px;
  }
}

.table__link {
  transition: opacity 0.3s;
}

.table__link:hover {
  opacity: .8;
}

.table__tbody td {
  padding: 19px 10px 19px 0;
  vertical-align: middle;
  font-size: 16px;
  color: #000;
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 400;
}

.table__tbody tr:not(:last-child) {
  border-bottom: 0.25px solid #B3B3B3;
}

.table__nowrap {
  white-space: nowrap;
}

.status.is-active {
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #3EDD9F;
  margin-top: -17px;
}

.status.is-back {
  background-image: url("../img/arr-back.svg");
  width: 10px;
  height: 13px;
  background-size: cover;
  margin-top: -7px;
}

.arrows {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.arrows__prev, .arrows__next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
}

.arrows .link__text {
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 500;
}

@media only screen and (max-width: 767px) {
  .arrows .link__text {
    display: none;
  }
}

.arrows .link__pic .icon {
  width: 8px;
  height: 22px;
}

.arrows__prev {
  left: 20px;
}

@media only screen and (max-width: 767px) {
  .arrows__prev {
    left: 2px;
  }
}

.arrows__prev .link__text {
  margin: 0 0 0 6px;
}

.arrows__prev .icon {
  transform: rotate(-180deg);
}

.arrows__next {
  right: 20px;
}

@media only screen and (max-width: 767px) {
  .arrows__next {
    right: 1px;
  }
}

.arrows__next .link__text {
  margin-right: 6px;
}

.timeline {
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  background: #000;
  padding: 8px 0;
}

@media only screen and (max-width: 767px) {
  .timeline {
    display: none;
  }
}

.timeline__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.timeline__item:last-child .timeline__text {
  color: #D8D8D8;
}

.timeline__pic {
  line-height: normal;
}

.timeline__pic .icon-check {
  margin-right: 10px;
  font-size: 18px;
  fill: #3EDD9F;
}

.timeline__pic .icon-arr-full {
  margin: 3px 7px 0 0;
  font-size: 15px;
  fill: #D8D8D8;
}

.timeline__pic, .timeline__text {
  display: inline-block;
  vertical-align: middle;
}

.timeline__text {
  color: #fff;
  font-size: 16px;
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 600;
}

.desc {
  max-width: 445px;
  margin: 40px auto 70px auto;
}

@media only screen and (max-width: 767px) {
  .desc {
    margin: 20px auto;
  }
}

.desc__head {
  padding: 23px 25px 12px;
  background: #DD124C;
  color: #fff;
}

.desc__head:after {
  content: " ";
  display: table;
  clear: both;
}

.desc__pic {
  float: left;
  margin-right: 17px;
}

.desc__caption {
  overflow: hidden;
  font-size: 20px;
  color: #fff;
  padding: 3px 0;
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 600;
}

.desc__content {
  background: #eaeaea;
  padding: 19px 32px 25px 28px;
  box-shadow: 0px 2px 4px 0 rgba(0, 0, 0, 0.15);
}

.desc__line {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 6px;
}

.desc__title {
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 500;
}

.desc__text p {
  font-size: 12px;
  line-height: 1.2;
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 500;
}

.desc__text p:not(:last-child) {
  margin-bottom: 16px;
}

.desc__text button {
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 600;
}

.label {
  display: inline-block;
  vertical-align: middle;
  padding: 0px 4px;
  line-height: 1;
  background: #fff;
}

.label .icon {
  width: 6.5px;
  height: 9px;
  fill: #000;
}

.label__text {
  font-size: 10px;
  color: #000;
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 500;
}

.label__pic, .label__text {
  display: inline-block;
  vertical-align: middle;
}

.human__ava {
  float: left;
  margin-right: 16px;
}

.human__ava img {
  width: 52px;
  height: 52px;
  overflow: hidden;
  border-radius: 50%;
  object-fit: cover;
}

.human__content {
  overflow: hidden;
}

.human__caption {
  font-size: 15px;
  color: #111;
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 500;
}

.human__meta {
  font-size: 14px;
  color: #111;
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 400;
}

.accordion__btn {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding-bottom: 9px;
  transition: opacity 0.3s;
}

.accordion__btn:hover {
  opacity: .8;
}

.accordion__icon {
  transform: rotate(180deg);
  transition: transform 0.15s ease-in-out;
}

.accordion__icon .icon {
  font-size: 7px;
  margin: 0px 2px 0 0;
  stroke: #000;
}

.accordion__item:not(:last-child) {
  margin-bottom: 35px;
}

.accordion__item.is-open .accordion__content {
  display: block;
}

.accordion__item.is-open .accordion__icon {
  transform-origin: 50% 50%;
  transform: rotate(0deg);
}

.accordion__title {
  font-family: "Rajdhani", Arial, Helvetica, sans-serif;
  font-weight: 600;
}

.accordion__content {
  display: none;
}
